.header {
	position: absolute;
	z-index: 10;

	&__logo {
		position: fixed;
		display: block;

		svg {
			width: 100%;
			height: 100%;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(35);
				top: vw(35);
				width: vw(25);
				height: vw(25);
			}

			@include min-aspect-ratio {
				left: vh(35);
				top: vh(35);
				width: vh(25);
				height: vh(25);
			}
		}

		@media (max-width: 1024px) {
			position: relative;

			@include max-aspect-ratio(320, 568) {
				width: vw(25, 320);
				height: vw(25, 320);
			}

			@include min-aspect-ratio(320, 568) {
				width: vh(25, 568);
				height: vh(25, 568);
			}
		}
	}

	&__test-text {
		position: fixed;
		font-weight: 300;
		font-style: italic;
		opacity: 0.4;
		transform: rotate(-90deg);
		transform-origin: top left;

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(22);
				top: vw(455);
				font-size: vw(12);
				line-height: vw(15);
			}

			@include min-aspect-ratio {
				left: vh(22);
				top: vh(455);
				font-size: vh(12);
				line-height: vh(15);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__menu-btn {
		margin: 0;
		border: none;
		border-radius: 50%;
		padding: 0;

		span {
			position: relative;
			display: block;
			margin: 0 auto;
			height: 2px;
			background-color: $color-black;
			transition: transform 0.1s;

			&::before,
			&::after {
				content: "";
				position: absolute;
				left: 0;
				width: 100%;
				height: 2px;
				background-color: $color-black;
			}

			&::before {
				@include aspect-ratio {
					@include max-aspect-ratio {
						top: vw(-6);
					}

					@include min-aspect-ratio {
						top: vh(-6);
					}
				}

				@media (max-width: 1024px) {
					@include max-aspect-ratio(320, 568) {
						top: vw(-6, 320);
					}

					@include min-aspect-ratio(320, 568) {
						top: vh(-6, 568);
					}
				}
			}

			&::after {
				@include aspect-ratio {
					@include max-aspect-ratio {
						bottom: vw(-6);
					}

					@include min-aspect-ratio {
						bottom: vh(-6);
					}
				}

				@media (max-width: 1024px) {
					@include max-aspect-ratio(320, 568) {
						bottom: vw(-6, 320);
					}

					@include min-aspect-ratio(320, 568) {
						bottom: vh(-6, 568);
					}
				}
			}

			@include aspect-ratio {
				@include max-aspect-ratio {
					width: vw(20);
				}

				@include min-aspect-ratio {
					width: vh(20);
				}
			}

			@media (max-width: 1024px) {
				@include max-aspect-ratio(320, 568) {
					width: vw(20, 320);
				}

				@include min-aspect-ratio(320, 568) {
					width: vh(20, 568);
				}
			}
		}

		@include aspect-ratio {
			background: transparent url("../images/header-button-bg.png") no-repeat center center;

			@include max-aspect-ratio {
				width: vw(54);
				height: vw(54);
			}

			@include min-aspect-ratio {
				width: vh(54);
				height: vh(54);
			}
		}
	}

	@include aspect-ratio {
		@include max-aspect-ratio {
			right: vw(80);
			top: vw(100);
		}

		@include min-aspect-ratio {
			right: vh(80);
			top: vh(100);
		}
	}

	@media (max-width: 1024px) {
		left: 0;
		right: 0;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;

		@include max-aspect-ratio(320, 568) {
			padding: vw(14, 320) vw(20, 320) 0 vw(19, 320);
		}

		@include min-aspect-ratio(320, 568) {
			padding: vh(14, 568) vh(20, 568) 0 vh(19, 568);
		}
	}
}
