.lead-title {
	position: absolute;
	left: 0;
	right: 0;
	top: 50%;
	bottom: 0;
	z-index: 31;
	pointer-events: none;

	&__text {
		position: relative;
		z-index: 3;
		font-family: $font-philosopher;
		text-align: center;
		color: $color-white;

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-bottom: vw(112);
				padding-left: vw(51);
				font-size: vw(140);
				line-height: vw(168);
				letter-spacing: vw(51);
			}

			@include min-aspect-ratio {
				margin-bottom: vh(112);
				padding-left: vh(51);
				font-size: vh(140);
				line-height: vh(168);
				letter-spacing: vh(51);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__round {
		position: relative;
		overflow: hidden;
		margin: 0 auto;
		text-align: center;

		img {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-left: vw(-80);
					width: vw(160);
					height: vw(160);
				}

				@include min-aspect-ratio {
					margin-left: vh(-80);
					width: vh(160);
					height: vh(160);
				}
			}

			@include desktop {
				position: absolute;
				left: 50%;
				top: 0;
			}

			@include tablet {
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				border-bottom: vw(10) solid $color-lemon;
				width: vw(212);
				height: vw(122);
			}

			@include min-aspect-ratio {
				border-bottom: vh(10) solid $color-lemon;
				width: vh(212);
				height: vh(122);
			}
		}

		@include tablet {
			position: absolute;
			left: 50%;

			@include max-aspect-ratio(320, 568) {
				bottom: vw(-56, 320);
				margin-left: vw(-55, 320);
				width: vw(110, 320);
				height: vw(110, 320);
			}

			@include min-aspect-ratio(320, 568) {
				bottom: vh(-56, 568);
				margin-left: vh(-55, 568);
				width: vh(110, 568);
				height: vh(110, 568);
			}
		}
	}

	&__trapeze {
		position: absolute;
		left: 50%;
		top: 0;
		z-index: 2;

		span {
			position: absolute;
			font-family: $font-philosopher;
			font-style: italic;
			white-space: nowrap;
			text-transform: lowercase;
			opacity: 0.5;
			transform: rotate(-90deg);
			transform-origin: top left;

			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(-23);
					top: vw(271);
					font-size: vw(9);
					line-height: vw(10);
				}

				@include min-aspect-ratio {
					left: vh(-23);
					top: vh(271);
					font-size: vh(9);
					line-height: vh(10);
				}
			}

			@include tablet {
				position: fixed;

				@include max-aspect-ratio(320, 568) {
					left: vw(22, 320);
					top: vw(291, 320);
					font-size: vw(9, 320);
					line-height: vw(10, 320);
				}

				@include min-aspect-ratio(320, 568) {
					left: vh(22, 568);
					top: vh(291, 568);
					font-size: vh(9, 568);
					line-height: vh(10, 568);
				}
			}
		}

		svg {
			width: 100%;
			height: 100%;
		}

		@include tablet {
			display: none;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-top: vw(-52);
				margin-left: vw(-375);
				width: vw(185);
				height: vw(290);
			}

			@include min-aspect-ratio {
				margin-top: vh(-52);
				margin-left: vh(-375);
				width: vh(185);
				height: vh(290);
			}
		}
	}

	&__affinage {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		overflow: hidden;
		font-family: $font-philosopher;
		font-weight: bold;

		@include aspect-ratio {
			@include max-aspect-ratio {
				font-size: vw(25);
				line-height: vw(25);
			}

			@include min-aspect-ratio {
				font-size: vh(25);
				line-height: vh(25);
			}
		}
	}

	&__small-text {
		position: absolute;
		left: 50%;
		bottom: 0;

		svg {
			position: absolute;
			opacity: 0.3;

			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(-5);
					top: vw(1);
					width: vw(25);
					height: vw(25);
				}

				@include min-aspect-ratio {
					left: vh(-5);
					top: vh(1);
					width: vh(25);
					height: vh(25);
				}
			}
		}

		&--1 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(31);
					margin-left: vw(-453);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(31);
					margin-left: vh(-453);
				}
			}
		}

		&--2 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(67);
					margin-left: vw(-388);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(67);
					margin-left: vh(-388);
				}
			}
		}

		&--3 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(22);
					margin-left: vw(-324);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(22);
					margin-left: vh(-324);
				}
			}
		}

		&--4 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(50);
					margin-left: vw(-265);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(50);
					margin-left: vh(-265);
				}
			}
		}

		&--5 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(10);
					margin-left: vw(-199);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(10);
					margin-left: vh(-199);
				}
			}
		}

		&--6 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(53);
					margin-left: vw(210);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(53);
					margin-left: vh(210);
				}
			}
		}

		&--7 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(45);
					margin-left: vw(312);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(45);
					margin-left: vh(312);
				}
			}
		}

		&--8 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(18);
					margin-left: vw(423);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(18);
					margin-left: vh(423);
				}
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__background-icon {
		position: absolute;
		left: 50%;
		bottom: 0;

		svg {
			width: 100%;
			height: 100%;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				width: vw(23);
				height: vw(23);
			}

			@include min-aspect-ratio {
				width: vh(23);
				height: vh(23);
			}
		}

		@include tablet {
			left: 50%;
			top: 50%;

			@include max-aspect-ratio(320, 568) {
				width: vw(23, 320);
				height: vw(23, 320);
			}

			@include min-aspect-ratio(320, 568) {
				width: vh(23, 568);
				height: vh(23, 568);
			}
		}

		&--empty {
			@include aspect-ratio {
				@include max-aspect-ratio {
					width: vw(27);
					height: vw(27);
				}

				@include min-aspect-ratio {
					width: vh(27);
					height: vh(27);
				}
			}

			@include tablet {
				@include max-aspect-ratio(320, 568) {
					width: vw(27, 320);
					height: vw(27, 320);
				}

				@include min-aspect-ratio(320, 568) {
					width: vh(27, 568);
					height: vh(27, 568);
				}
			}
		}

		&--1 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(84);
					margin-left: vw(-528);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(84);
					margin-left: vh(-528);
				}
			}

			@include tablet {
				@include max-aspect-ratio(320, 568) {
					margin-top: vw(-57, 320);
					margin-left: vw(109, 320);
				}

				@include min-aspect-ratio(320, 568) {
					margin-top: vh(-57, 568);
					margin-left: vh(109, 568);
				}
			}
		}

		&--2 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(-6);
					margin-left: vw(-271);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(-6);
					margin-left: vh(-271);
				}
			}

			@include tablet {
				@include max-aspect-ratio(320, 568) {
					margin-top: vw(229, 320);
					margin-left: vw(-117, 320);
				}

				@include min-aspect-ratio(320, 568) {
					margin-top: vh(229, 568);
					margin-left: vh(-117, 568);
				}
			}
		}

		&--3 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(20);
					margin-left: vw(243);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(20);
					margin-left: vh(243);
				}
			}

			@include tablet {
				display: none;
			}
		}

		&--4 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(42);
					margin-left: vw(480);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(42);
					margin-left: vh(480);
				}
			}

			@include tablet {
				display: none;
			}
		}
	}

	@include aspect-ratio {
		@include max-aspect-ratio {
			margin-top: vw(-86);
		}

		@include min-aspect-ratio {
			margin-top: vh(-86);
		}
	}

	@include tablet {
		top: 0;
		z-index: 10;
		overflow: hidden;
	}
}
