.slider {
	width: 100%;
	height: 100%;

	&__container {
		position: relative;
		width: 100%;
		height: 100%;
		background-color: $color-lemon;

		.swiper-container {
			width: 100%;
			height: 100%;
		}
	}

	&__title {
		position: absolute;
		z-index: 5;
		display: flex;
		flex-flow: column nowrap;
		justify-content: center;
		border: 2px solid $color-black;
		border-radius: 50%;
		font-family: $font-geometria;
		text-transform: uppercase;
		color: $color-white;

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(85);
				top: vw(31);
				padding-left: vw(25);
				width: vw(102);
				height: vw(102);
				font-size: vw(17);
				line-height: vw(21);
			}

			@include min-aspect-ratio {
				left: vh(85);
				top: vh(31);
				padding-left: vh(25);
				width: vh(102);
				height: vh(102);
				font-size: vh(17);
				line-height: vh(21);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__slide {
		position: relative;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 3;
			display: block;
			background-color: $color-lemon;
			opacity: 0.8;
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__back-slider {
		@include desktop {
			position: absolute;
			left: 50%;
			top: 50%;
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				margin-top: vw(-303);
				margin-left: vw(-640);
				width: vw(800);
				height: vw(525);
			}

			@include min-aspect-ratio {
				margin-top: vh(-303);
				margin-left: vh(-640);
				width: vh(800);
				height: vh(525);
			}
		}

		@include tablet {
			position: absolute;
			top: 14.1vh;
			margin-top: 0;
			margin-left: 0;
			width: 100%;
			height: 30vh;

			@include max-aspect-ratio(320, 568) {
				left: vw(-44, 320);
				width: vw(280, 320);
			}

			@include min-aspect-ratio(320, 568) {
				left: vh(-44, 568);
				width: vh(280, 568);
			}

			@media (orientation: landscape) {
				left: -10vw;
				top: 20vh;
				width: 60vw;
				height: 60vh;
			}
		}
	}

	&__front-slider {
		position: relative;
		z-index: 3;

		@include desktop {
			position: absolute;
			left: 50%;
			top: 50%;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-top: vw(-216);
					margin-left: vw(-220);
					width: vw(860);
					height: vw(490);
				}

				@include min-aspect-ratio {
					margin-top: vh(-216);
					margin-left: vh(-220);
					width: vh(860);
					height: vh(490);
				}
			}
		}

		@include tablet {
			margin-top: 44.1vh;
			width: 100%;
			height: 55.9vh;

			@media (orientation: landscape) {
				margin-top: 30vh;
				margin-left: 25vw;
				width: 75vw;
				height: 70vh;
			}
		}
	}

	&__ui {
		position: absolute;
		z-index: 4;
		user-select: none;

		@include aspect-ratio {
			background-color: $color-lemon;

			@include max-aspect-ratio {
				right: vw(198);
				top: vw(72);
				bottom: vw(35);
				padding: vw(308) vw(22) 0;
				min-width: vw(140);
			}

			@include min-aspect-ratio {
				right: vh(198);
				top: vh(72);
				bottom: vh(35);
				padding: vh(308) vh(22) 0;
				min-width: vh(140);
			}
		}

		@include tablet {
			position: static;
			width: 0;
			height: 0;
		}
	}

	&__arrow {
		position: absolute;
		z-index: 4;
		opacity: 0.4;
		transition: opacity 0.2s;
		cursor: pointer;

		svg {
			width: 100%;
			height: 100%;
		}

		&:hover {
			opacity: 0.7;
		}

		&--left {
			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(-37);
				}

				@include min-aspect-ratio {
					left: vh(-37);
				}
			}

			@include tablet {
				@include max-aspect-ratio(320, 568) {
					left: vw(20, 320);
				}

				@include min-aspect-ratio(320, 568) {
					left: vh(20, 568);
				}
			}
		}

		&--right {
			@include aspect-ratio {
				@include max-aspect-ratio {
					right: vw(-39);
				}

				@include min-aspect-ratio {
					right: vh(-39);
				}
			}

			@include tablet {
				z-index: 10;

				@include max-aspect-ratio(320, 568) {
					right: vw(20, 320);
				}

				@include min-aspect-ratio(320, 568) {
					right: vh(20, 568);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				top: vw(227);
				width: vw(22);
				height: vw(38);
			}

			@include min-aspect-ratio {
				top: vh(227);
				width: vh(22);
				height: vh(38);
			}
		}

		@include tablet {
			@include max-aspect-ratio(320, 568) {
				bottom: vw(138, 320);
				width: vw(22, 320);
				height: vw(38, 320);
			}

			@include min-aspect-ratio(320, 568) {
				bottom: vh(138, 568);
				width: vh(22, 568);
				height: vh(38, 568);
			}
		}
	}

	&__pagination {
		position: relative;
		display: inline-block;

		@include tablet {
			position: static;
		}
	}

	&__total {
		font-weight: 900;
		font-style: italic;

		&::before {
			@include desktop {
				content: "";
				position: absolute;
				bottom: 0;
				display: block;
				background-color: $color-black;
			}

			@include aspect-ratio {
				@include max-aspect-ratio {
					right: vw(-19);
					width: vw(17);
					height: vw(3);
				}

				@include min-aspect-ratio {
					right: vh(-19);
					width: vh(17);
					height: vh(3);
				}
			}

			@include tablet {
				content: "";
				position: absolute;
				right: 0;
				bottom: 0;
				z-index: -1;
				display: block;

				@include max-aspect-ratio(320, 568) {
					border: vw(52, 320) solid transparent;
					border-right-color: $color-lemon;
					border-bottom-color: $color-lemon;
				}

				@include min-aspect-ratio(320, 568) {
					border: vh(52, 568) solid transparent;
					border-right-color: $color-lemon;
					border-bottom-color: $color-lemon;
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				font-size: vw(45);
				line-height: vw(32);
				letter-spacing: vw(5);
			}

			@include min-aspect-ratio {
				font-size: vh(45);
				line-height: vh(32);
				letter-spacing: vh(5);
			}
		}

		@include tablet {
			position: absolute;
			right: 0;
			bottom: 0;
			z-index: 5;

			@include max-aspect-ratio(320, 568) {
				padding-right: vw(20, 320);
				padding-bottom: vw(17, 320);
				font-size: vw(22, 320);
				line-height: vw(20, 320);
			}

			@include min-aspect-ratio(320, 568) {
				padding-right: vh(20, 568);
				padding-bottom: vh(17, 568);
				font-size: vh(22, 568);
				line-height: vh(30, 568);
			}
		}
	}

	&__current {
		position: absolute;
		right: 0;
		font-weight: 900;
		font-style: italic;
		text-align: right;
		transform: translateX(100%);

		@include aspect-ratio {
			@include max-aspect-ratio {
				top: vw(38);
				margin-left: vw(4);
				font-size: vw(17);
				line-height: vw(17);
			}

			@include min-aspect-ratio {
				top: vh(38);
				margin-left: vh(4);
				font-size: vh(17);
				line-height: vh(17);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__text {
		position: absolute;
		z-index: 4;
		font-weight: 300;

		span {
			display: block;

			@include aspect-ratio {
				@include max-aspect-ratio {
					margin-bottom: vw(63);
				}

				@include min-aspect-ratio {
					margin-bottom: vh(63);
				}
			}
		}

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(143);
				top: vw(85);
				font-size: vw(14);
				line-height: vw(16);
			}

			@include min-aspect-ratio {
				left: vh(143);
				top: vh(85);
				font-size: vh(14);
				line-height: vh(16);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__background-image {
		position: absolute;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		&--1 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(482);
					top: vw(-124);
					width: vw(59);
					height: vw(58);
				}

				@include min-aspect-ratio {
					left: vh(482);
					top: vh(-124);
					width: vh(59);
					height: vh(58);
				}
			}

			@include tablet {
				z-index: 3;

				@include max-aspect-ratio(320, 568) {
					top: vw(-12, 320);
					margin-left: vw(-90, 320);
					width: vw(45, 320);
					height: vw(45, 320);
				}

				@include min-aspect-ratio(320, 568) {
					top: vh(-12, 568);
					margin-left: vh(-90, 568);
					width: vh(45, 568);
					height: vh(45, 568);
				}
			}
		}

		&--2 {
			z-index: 4;

			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(569);
					top: vw(35);
					width: vw(54);
					height: vw(60);
				}

				@include min-aspect-ratio {
					left: vh(569);
					top: vh(35);
					width: vh(54);
					height: vh(60);
				}
			}

			@include tablet {
				z-index: -2;

				@include max-aspect-ratio(320, 568) {
					top: vw(-28, 320);
					margin-left: vw(-10, 320);
					width: vw(42, 320);
					height: vw(46, 320);
				}

				@include min-aspect-ratio(320, 568) {
					top: vh(-28, 568);
					margin-left: vh(-10, 568);
					width: vh(42, 568);
					height: vh(46, 568);
				}
			}
		}

		&--3 {
			@include aspect-ratio {
				@include max-aspect-ratio {
					left: vw(661);
					top: vw(-42);
					width: vw(43);
					height: vw(58);
				}

				@include min-aspect-ratio {
					left: vh(661);
					top: vh(-42);
					width: vh(43);
					height: vh(58);
				}
			}

			@include tablet {
				@include max-aspect-ratio(320, 568) {
					top: vw(-106, 320);
					margin-left: vw(62, 320);
					width: vw(33, 320);
					height: vw(44, 320);
				}

				@include min-aspect-ratio(320, 568) {
					top: vh(-106, 568);
					margin-left: vh(62, 568);
					width: vh(33, 568);
					height: vh(44, 568);
				}
			}
		}

		@include tablet {
			left: 50%;
		}
	}

	&__text-vertical {
		position: absolute;
		z-index: 4;
		font-family: $font-geometria;
		font-weight: 900;
		opacity: 0.5;
		transform: rotate(-90deg);
		transform-origin: top left;

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(481);
				top: vw(64);
				font-size: vw(28);
				line-height: vw(28);
				letter-spacing: vw(2.7);
			}

			@include min-aspect-ratio {
				left: vh(481);
				top: vh(64);
				font-size: vh(28);
				line-height: vh(28);
				letter-spacing: vh(2.7);
			}
		}

		@include tablet {
			display: none;
		}
	}

	&__trapeze {
		display: none;
		overflow: hidden;

		svg {
			width: 100%;
			height: 100%;
		}

		@include tablet {
			position: absolute;
			right: 0;
			top: 0;
			display: block;

			@include max-aspect-ratio(320, 568) {
				width: vw(286, 320);
				height: vw(181, 320);
			}

			@include min-aspect-ratio(320, 568) {
				width: vh(286, 568);
				height: vh(181, 568);
			}
		}
	}

	&__trapeze-inner {
		position: absolute;
		width: 100%;
		height: 100%;

		@include max-aspect-ratio(320, 568) {
			right: vw(-128, 320);
			top: vw(-44, 320);
		}

		@include min-aspect-ratio(320, 568) {
			right: vh(-128, 568);
			top: vh(-44, 568);
		}
	}

	@media (max-width: 1024px) {
		display: flex;
		flex-flow: column nowrap;
		align-items: flex-start;

		@media (orientation: landscape) {
			flex-flow: row nowrap;
		}
	}
}
