@mixin aspect-ratio($min-width: 1025px, $min-height: 1200px) {
	@media (min-width: $min-width), (min-height: $min-height) {
		@content;
	}
}

@mixin max-aspect-ratio($maxRatio: 1440, $minRatio: 800) {
	@media (max-aspect-ratio: #{$maxRatio} / #{$minRatio}) {
		@content;
	}
}

@mixin min-aspect-ratio($maxRatio: 1440, $minRatio: 800) {
	@media (min-aspect-ratio: #{$maxRatio} / #{$minRatio}) {
		@content;
	}
}
