.main {
	position: relative;
	z-index: 1;
	height: 100vh;
	background-color: $color-white-600;

	&::before {
		content: "";
		position: absolute;
		right: 50%;
		bottom: 0;
		z-index: 1;
		display: none;
		background-color: linear-gradient(to top, $color-white, transparent);

		@include aspect-ratio {
			@include max-aspect-ratio {
				left: vw(120);
				top: vw(249);
				box-shadow: 0 0 vw(98) 0 rgba($color-black, 0.1);
			}

			@include min-aspect-ratio {
				left: vh(120);
				top: vh(249);
				box-shadow: 0 0 vh(98) 0 rgba($color-black, 0.1);
			}
		}

		@include desktop {
			display: block;
		}
	}

	&::after {
		content: "";
		position: absolute;
		left: 50%;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 2;
		display: none;
		background-color: $color-white;

		@include aspect-ratio {
			@include max-aspect-ratio {
				box-shadow: 0 0 vw(86) 0 rgba($color-black, 0.1);
			}

			@include min-aspect-ratio {
				box-shadow: 0 0 vh(86) 0 rgba($color-black, 0.1);
			}
		}

		@include desktop {
			display: block;
		}
	}

	@include tablet {
		background-color: $color-lemon;
	}
}

